


.header {
  &__h1 {
    text-align: center;
    font-size: 2.4em;
  }
}

.wrapper {
  padding: 15px 5%;
  margin: 15px auto;
  max-width: 625px;

  & .calendar {
    margin: 15px 0px;
  }
}


.button {
  width: 25px;
  height: 25px;
  border-radius: 100%;
  background-color: green;
  border: 1px solid green;
  transition: .4s;
  font-size: .8em;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin: 5px 0px;
  
  &:hover {
      border-color: white;
  }
}


input {
  border: 1px solid #777;
  border-radius: 2px;
  padding: 5px;
}